* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #f0f0f0;
  /* Светло-серый цвет текста для лучшей читаемости */
  background-color: #121212;
  /* Очень темный серо-черный цвет */
}
.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}
header {
  background: #000;
  color: #fff;
  padding: 10px 0;
}
header .logo {
  font-size: 1.5em;
  cursor: pointer;
  font-weight: bold;
}
header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}
header nav .btn {
  background: #d65900;
  /* Более приглушенный коричнево-оранжевый оттенок */
  color: #fff;
  padding: 8px 15px;
  border-radius: 5px;
  text-decoration: none;
}
.price s {
  color: #666;
  text-decoration-color: #ff4500;
  font-size: 0.7em;
}
.discount {
  color: #ff4500;
  font-weight: bold;
  margin-left: 10px;
}
.plan-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.plan-badge {
  width: max-content;
  background-color: #b12f00;
  color: white;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 0.8em;
  margin-bottom: 5px;
}
#pricing .container h2 {
  text-align: center;
  margin-bottom: 30px;
  /* Опционально, для отступа между заголовком и тарифами */
}
#hero {
  background: linear-gradient(to right, #000046, #1cb5e0);
  color: #fff;
  text-align: center;
  padding: 50px 0;
}
#hero h1 {
  font-size: 2.5em;
}
#hero .btn-primary {
  background: #ff4500;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 20px;
  display: inline-block;
}
#about,
#pricing,
#faq {
  margin-top: 10px;
  /* Добавил отступ сверху */
  padding: 10px 0;
}
#about .container {
  text-align: center;
}
#about .container h2 {
  margin-bottom: 20px;
  /* Увеличьте значение по вашему усмотрению */
  text-align: center;
  /* Если хотите, чтобы заголовок был по центру */
}
.features {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
}
.feature {
  background-color: #1a1a1a;
  text-align: center;
  max-width: 250px;
  padding: 20px;
  transition: transform 0.3s ease;
}
.feature:hover {
  transform: scale(1.05);
}
.feature img {
  width: 50px;
  height: 50px;
  margin-bottom: 15px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg) brightness(102%) contrast(102%);
}
.feature h3 {
  margin-bottom: 10px;
  color: #c9c9c9;
}
.feature p {
  color: #666;
}
.plans {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}
.plan {
  background: #1e1e1e;
  /* Темнее, чем основной фон */
  color: #c9c9c9;
  padding: 30px;
  text-align: center;
  border-radius: 10px;
  width: 300px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}
.plan:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
}
.plan .price {
  font-size: 1.5em;
  color: #ff4500;
  margin: 10px 0;
}
.plan .btn-primary {
  margin-top: 10px;
  color: #666;
  /* Цвет текста кнопок "Оплатить" */
  background-color: transparent;
  /* Прозрачный фон */
}
.plan .btn-primary:hover {
  background-color: rgba(102, 102, 102, 0.1);
  /* Легкое затемнение при наведении */
}
footer {
  background: #000;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  margin-top: 20px;
}
footer .links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
footer a {
  color: #666;
  text-decoration: none;
}
footer a:hover {
  text-decoration: underline;
}
.faq-item {
  border-color: #333;
  margin-bottom: 10px;
  border: 1px solid #4b4b4b;
  border-radius: 5px;
}
.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  background-color: #1e1e1e;
  transition: background-color 0.3s;
}
.faq-question:hover {
  background-color: #111111;
}
.faq-question::after {
  content: '▼';
  transition: transform 0.3s;
}
.faq-question.active::after {
  transform: rotate(180deg);
}
.faq-answer {
  white-space: pre-line;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding 0.3s;
  padding: 0 15px;
  background-color: #161616;
}
.faq-answer a {
  color: white;
}
.faq-answer.show {
  max-height: 500px;
  padding: 15px;
}
.footerLicense {
  font-size: 0.8em;
  color: #242424;
  margin-top: 10px;
}
#payment .payment-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-top: 50px;
}
.payment-form,
.payment-info {
  background: #1e1e1e;
  padding: 20px;
  border-radius: 10px;
  width: 50%;
}
.payment-form .btn-primary {
  background: #d65900;
  /* Коричнево-оранжевый оттенок */
  text-decoration: none;
  /* Убираем подчеркивание */
  box-sizing: border-box;
  /* Учитываем отступы в ширине */
  color: #fff;
  padding: 8px 15px;
  border-radius: 5px;
  margin-top: 10px;
  width: 100%;
}
.payment-form .btn-primary:hover {
  background: #b12f00;
  /* Немного темнее при наведении */
}
.form-group {
  margin-bottom: 20px;
}
.form-group label {
  display: block;
  margin-bottom: 10px;
  color: #c9c9c9;
}
.form-group input[type='text'] {
  width: 100%;
  padding: 10px;
  background: #121212;
  border: 1px solid #333;
  color: #f0f0f0;
  border-radius: 5px;
}
.plan-selection {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.plan-selection input[type='radio'] {
  display: none;
}
.plan-selection .plan-price s {
  color: #666;
  text-decoration-color: #ff4500;
}
.plan-option {
  display: flex;
  flex-direction: column;
  background: #121212;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.plan-selection input[type='radio']:checked + .plan-option {
  background: #2a2a2a;
  border: 1px solid #d65900;
}
.plan-option .plan-title {
  font-weight: bold;
  color: #c9c9c9;
}
.plan-option .plan-price {
  color: #ff4500;
  margin: 5px 0;
}
.plan-option .plan-badge {
  color: #e7e7e7;
  /* Белый цвет текста */
  font-size: 0.8em;
}
.payment-info ol {
  color: #c9c9c9;
  padding-left: 20px;
  margin-bottom: 20px;
}
.support-link {
  text-align: center;
}
.amount-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
}
#amount {
  flex-grow: 1;
  padding: 10px;
  background: #121212;
  border: 1px solid #333;
  color: #f0f0f0;
  border-radius: 5px;
}
.quick-amount-buttons {
  display: flex;
  gap: 10px;
}
.quick-amount-btn {
  background: #1e1e1e;
  color: #c9c9c9;
  border: 1px solid #333;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.quick-amount-btn:hover {
  background-color: #2a2a2a;
}
.quick-amount-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}
.quick-amount-row-1,
.quick-amount-row-2 {
  display: flex;
  gap: 10px;
}
.label-large.amount-label {
  margin-top: 30px;
  /* Увеличиваем отступ сверху на 30 пикселей */
}
