av {\n    flex-direction: column;\n    text-align: center;\n  }\n\n  header .logo {\n    margin-bottom: 10px;\n  }\n\n  header nav .btn {\n    margin-top: 10px;\n  }\n\n  #faq .faq-item {\n    width: 100%;\n  }\n\n  .plan .price {\n    font-size: 1.4em;\n  }\n\n  .plan-badge {\n    display: block;\n    margin-top: 10px;\n    font-size: 0.9em;\n  }\n}\n\n/* Еще более мелкие экраны */\n@media screen and (max-width: 480px) {\n  body {\n    font-size: 14px; /* Немного увеличим размер */\n  }\n\n  #hero h1 {\n    font-size: 1.7em;\n  }\n\n  .plan-badge {\n    font-size: 0.8em;\n  }\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  font-family: Arial, sans-serif;\n  line-height: 1.6;\n  color: #f0f0f0; /* Светло-серый цвет текста для лучшей читаемости */\n  background-color: #121212; /* Очень темный серо-черный цвет */\n}\n\n.container {\n  width: 90%;\n  max-width: 1200px;\n  margin: 0 auto;\n}\n\nheader {\n  background: #000;\n  color: #fff;\n  padding: 10px 0;\n}\n\nheader .logo {\n  font-size: 1.5em;\n  cursor: pointer;\n  font-weight: bold;\n}\n\nheader nav {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\nheader nav a {\n  color: #fff;\n  text-decoration: none;\n  margin: 0 10px;\n}\n\nheader nav .btn {\n  background: #d65900; /* Более приглушенный коричнево-оранжевый оттенок */\n  color: #fff;\n  padding: 8px 15px;\n  border-radius: 5px;\n  text-decoration: none;\n}\n\n.price s {\n  color: #666;\n  text-decoration-color: #ff4500;\n  font-size: 0.7em;\n}\n\n.discount {\n  color: #ff4500;\n  font-weight: bold;\n  margin-left: 10px;\n}\n\n.plan-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.plan-badge {\n  width: max-content;\n  background-color: #b12f00;\n  color: white;\n  padding: 3px 8px;\n  border-radius: 4px;\n  font-size: 0.8em;\n  margin-bottom: 5px;\n}\n\n#pricing .container h2 {\n  text-align: center;\n  margin-bottom: 30px; /* Опционально, для отступа между заголовком и тарифами */\n}\n\n#hero {\n  background: linear-gradient(to right, #000046, #1cb5e0);\n  color: #fff;\n  text-align: center;\n  padding: 50px 0;\n}\n\n#hero h1 {\n  font-size: 2.5em;\n}\n\n#hero .btn-primary {\n  background: #ff4500;\n  color: #fff;\n  padding: 10px 20px;\n  border-radius: 5px;\n  text-decoration: none;\n  margin-top: 20px;\n  display: inline-block;\n}\n\n#about,\n#pricing,\n#faq {\n  margin-top: 10px; /* Добавил отступ сверху */\n  padding: 10px 0;\n}\n\n#about .container {\n  text-align: center;\n}\n\n#about .container h2 {\n  margin-bottom: 20px; /* Увеличьте значение по вашему усмотрению */\n  text-align: center; /* Если хотите, чтобы заголовок был по центру */\n}\n\n.features {\n  display: flex;\n  justify-content: center;\n  gap: 40px;\n  flex-wrap: wrap;\n}\n\n.feature {\n  background-color: #1a1a1a;\n  text-align: center;\n  max-width: 250px;\n  padding: 20px;\n  transition: transform 0.3s ease;\n}\n\n.feature:hover {\n  transform: scale(1.05);\n}\n\n.feature img {\n  width: 50px;\n  height: 50px;\n  margin-bottom: 15px;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg)\n    brightness(102%) contrast(102%);\n}\n\n.feature h3 {\n  margin-bottom: 10px;\n  color: rgb(201, 201, 201);\n}\n\n.feature p {\n  color: #666;\n}\n\n.plans {\n  display: flex;\n  justify-content: center;\n  gap: 30px;\n  flex-wrap: wrap;\n}\n\n.plan {\n  background: #1e1e1e; /* Темнее, чем основной фон */\n  color: rgb(201, 201, 201);\n  padding: 30px;\n  text-align: center;\n  border-radius: 10px;\n  width: 300px;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\n  transition:\n    transform 0.3s ease,\n    box-shadow 0.3s ease;\n  display: flex;\n  flex-direction: column;\n  align-content: center;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.plan:hover {\n  transform: translateY(-10px);\n  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);\n}\n\n.plan .price {\n  font-size: 1.5em;\n  color: #ff4500;\n  margin: 10px 0;\n}\n\n.plan .btn-primary {\n  margin-top: 10px;\n  color: #666; /* Цвет текста кнопок \"Оплатить\" */\n  background-color: transparent; /* Прозрачный фон */\n}\n\n.plan .btn-primary:hover {\n  background-color: rgba(\n    102,\n    102,\n    102,\n    0.1\n  ); /* Легкое затемнение при наведении */\n}\n\nfooter {\n  background: #000;\n  color: #fff;\n  text-align: center;\n  padding: 10px 0;\n  margin-top: 20px;\n  .links {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    align-content: center;\n    justify-content: center;\n    align-items: center;\n    gap: 10px;\n  }\n}\n\nfooter a {\n  color: #666;\n  text-decoration: none;\n  // display: block;\n}\n\nfooter a:hover {\n  text-decoration: underline;\n}\n\n.faq-item {\n  border-color: #333;\n  margin-bottom: 10px;\n  border: 1px solid #4b4b4b;\n  border-radius: 5px;\n}\n\n.faq-question {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 15px;\n  cursor: pointer;\n  background-color: #1e1e1e;\n  transition: background-color 0.3s;\n}\n\n.faq-question:hover {\n  background-color: #111111;\n}\n\n.faq-question::after {\n  content: '▼';\n  transition: transform 0.3s;\n}\n\n.faq-question.active::after {\n  transform: rotate(180deg);\n}\n\n.faq-answer {\n  white-space: pre-line;\n  max-height: 0;\n  overflow: hidden;\n  transition:\n    max-height 0.3s ease-out,\n    padding 0.3s;\n  padding: 0 15px;\n  background-color: #161616;\n  a {\n    color: white;\n  }\n}\n\n.faq-answer.show {\n  max-height: 500px;\n  padding: 15px;\n}\n.footerLicense {\n  font-size: 0.8em;\n  color: #242424;\n  margin-top: 10px;\n}\n\n#payment .payment-wrapper {\n  display: flex;\n  justify-content: space-between;\n  gap: 40px;\n  margin-top: 50px;\n}\n\n.payment-form,\n.payment-info {\n  background: #1e1e1e;\n  padding: 20px;\n  border-radius: 10px;\n  width: 50%;\n}\n\n.payment-form .btn-primary {\n  background: #d65900; /* Коричнево-оранжевый оттенок */\n  text-decoration: none; /* Убираем подчеркивание */\n  box-sizing: border-box; /* Учитываем отступы в ширине */\n  color: #fff;\n  padding: 8px 15px;\n  border-radius: 5px;\n  margin-top: 10px;\n  width: 100%;\n}\n\n.payment-form .btn-primary:hover {\n  background: #b12f00; /* Немного темнее при наведении */\n}\n\n.form-group {\n  margin-bottom: 20px;\n}\n\n.form-group label {\n  display: block;\n  margin-bottom: 10px;\n  color: #c9c9c9;\n}\n\n.form-group input[type='text'] {\n  width: 100%;\n  padding: 10px;\n  background: #121212;\n  border: 1px solid #333;\n  color: #f0f0f0;\n  border-radius: 5px;\n}\n\n.plan-selection {\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n}\n\n.plan-selection input[type='radio'] {\n  display: none;\n}\n\n.plan-selection .plan-price s {\n  color: #666;\n  text-decoration-color: #ff4500;\n}\n\n.plan-option {\n  display: flex;\n  flex-direction: column;\n  background: #121212;\n  padding: 15px;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n\n.plan-selection input[type='radio']:checked + .plan-option {\n  background: #2a2a2a;\n  border: 1px solid #d65900;\n}\n\n.plan-option .plan-title {\n  font-weight: bold;\n  color: #c9c9c9;\n}\n\n.plan-option .plan-price {\n  color: #ff4500;\n  margin: 5px 0;\n}\n\n.plan-option .plan-badge {\n  color: #e7e7e7; /* Белый цвет текста */\n  font-size: 0.8em;\n}\n\n.payment-info ol {\n  color: #c9c9c9;\n  padding-left: 20px;\n  margin-bottom: 20px;\n}\n\n.support-link {\n  text-align: center;\n}\n\n.amount-wrapper {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n}\n\n#amount {\n  flex-grow: 1;\n  padding: 10px;\n  background: #121212;\n  border: 1px solid #333;\n  color: #f0f0f0;\n  border-radius: 5px;\n}\n\n.quick-amount-buttons {\n  display: flex;\n  gap: 10px;\n}\n\n.quick-amount-btn {\n  background: #1e1e1e;\n  color: #c9c9c9;\n  border: 1px solid #333;\n  padding: 8px 15px;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n\n.quick-amount-btn:hover {\n  background-color: #2a2a2a;\n}\n\n.quick-amount-buttons {\n  display: flex;\n  gap: 10px;\n  margin-top: 10px;\n}\n\n.quick-amount-row-1,\n.quick-amount-row-2 {\n  display: flex;\n  gap: 10px;\n}\n\n.label-large.amount-label {\n  margin-top: 30px; /* Увеличиваем отступ сверху на 30 пикселей */\n}\n",".contacts {\n  background: black;\n  display: flex;\n  flex-direction: column;\n  flex-wrap: wrap;\n  align-content: center;\n  align-items: flex-start;\n  & > div {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    align-content: center;\n    justify-content: flex-start;\n    gap: 10px;\n    .title {\n      &::after {\n        content: ':';\n        color: grey;\n        margin-left: 3px;\n        font-weight: 800;\n      }\n    }\n    a {\n      color: white;\n    }\n  }\n}\n"],"sourceRoot":""}]);
